$color: grey;
.todoItem {
    border: 1px solid $color;
    list-style-type: none;
    padding: 5px;
    .itemText {
        color: blue;
    }
    &Done {
        &>span{
            text-decoration: line-through;
        }
        
    }
    button{
        &:first-of-type{
            margin-left: auto;
        }  
        &:last-of-type{
            background: tomato;
        }
    }
}
